<template lang="html">
  <div class="detail-hd">
    <div class="detail-hd__box">
      <img class="detail-hd__img" :src="headPic" alt="">
      <div v-if="showMsg" class="detail-hd__msg">售后评价后可查看素颜照</div>
    </div>
  </div>
</template>

<script>

export default {
  props: {
    // 页头图片必填
    headPic: {
      type: String,
      default: ''
    },
    // 显示提示语
    showMsg: {
      type: Boolean,
      default: true
    }
  },
  methods: {
    testst () {
      this.$emit('testsst')
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';

  .detail-hd {
    border-bottom: 1px solid @lineColor;
    padding: 0.46rem 0;
    .detail-hd__box {
      margin: 0 auto;
      width: 1.4rem;
      position: relative;
      height: 1.4rem;
      .detail-hd__img {
        width: 1.4rem;
        height: 1.4rem;
        border-radius: 50%;
      }
      .detail-hd__msg {
        position: absolute;
        width:2rem;
        left: 1.6rem;
        top: 0.32rem;
        color: @activeColor;
        font-size: .3rem;
        line-height: 0.4rem;
      }
    }
  }
</style>



