<template lang="html">
  <div class="sever-block">
    <div class="l">
      <div class="hd">我的评价</div>
      <div class="msg">服务结束后方能评价</div>
    </div>
    <div class="start r">
      <!-- <flm-star :readonly="true" amount="3.5"></flm-star> -->
      <span class="vote-star"><i :style="{width: percent}"></i></span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    percent: {
      type: String,
      default: '0'
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';

  .sever-block {
    margin-top: .3rem;
    overflow: hidden;
    background: @whiteColor;
    padding: .2rem;
    text-align: center;
    .l {
      width: 50%;
      float: left;
      border-right: 1px solid @lineColor;
      color: @midleColor;
      font-size: .32rem;
      line-height: .6rem;
      .msg {
        color: @redColor;
      }
    }
    .r {
      float: left;
      padding-top: .4rem;
      padding-left: .4rem;
    }
    .start {
      overflow: hidden;
      .vote-star {
        display: inline-block;
        position: relative;
        width: 3rem;
        height: .43rem;
        background: url('../../assets/img/l-star02@3x.png') no-repeat top left;
        background-size: cover;
        i {
          position: absolute;
          top: 0;
          left: 0;
          display: inline-block;
          width: 30%;
          height: .43rem;
          background: url('../../assets/img/l-star@3x.png') no-repeat top left;
          background-size: cover;
        }
      }
    }
  }
</style>



