<template lang="html">
  <div class="shop-appraise">
    <div class="total">
      <div class="hd">商家评价总次数</div>
      <div class="num">{{totalScore}}</div>
    </div>
    <div class="start-line">
      <div class="box">
        <div class="text">服务评价</div>
        <div class="start">
          <star-line :width="servicePercent"></star-line>
        </div>
      </div>
      <div class="box">
        <div class="text">电话接通率</div>
        <div class="start">
          <star-line :width="phonePercent"></star-line>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import starLine from '@/components/common/starLine'
export default {
  props: {
    servicePercent: {
      type: String,
      default: '0'
    },
    phonePercent: {
      type: String,
      default: '0'
    },
    totalScore: {
      type: String,
      default: '48'
    }
  },
  components: { starLine }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';
.shop-appraise {
  padding: .84rem 0;
  overflow: hidden;
  font-size: .32rem;
  line-height: .6rem;
  .total {
    color: @defaultColor;
    float: left;
    text-align: center;
    width: 2.6rem;
    border-right: 1px solid @lineColor;
    .num {
      color: @redColor;
    }
  }
  .start-line {
    overflow: hidden;
    padding-left: .5rem;
    .text {
      width: 1.7rem;
      float: left;
    }
    .start {
      overflow: hidden;
      .vote-star {
        display: inline-block;
        position: relative;
        width: 2.06rem;
        height: .3rem;
        background: url('../../assets/img/l-star02@3x.png') no-repeat top left;
        background-size: cover;
        i {
          position: absolute;
          top: 0;
          left: 0;
          display: inline-block;
          width: 30%;
          height: .3rem;
          background: url('../../assets/img/l-star@3x.png') no-repeat top left;
          background-size: cover;
        }
      }
    }
  }
}
</style>



