<template lang="html">
  <div class="star-line-box">
    <span class="gray-star"><i :style="{'width': width}" class="yellow-star"></i></span>
  </div>
</template>

<script>
export default {
  props: {
    width: {
      type: String,
      default: '0%'
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
.star-line-box {
  display: inline-block;
  // overflow: hidden;
  position: relative;
  .gray-star {
    display: inline-block;
    position: relative;
    width: 2.1rem;
    height: .304rem;
    background: url('../../assets/img/l-star02@3x.png') no-repeat top left;
    background-size: cover;
    .yellow-star {
      position: absolute;
      top: 0;
      left: 0;
      display: inline-block;
      width: 100%;
      height: .304rem;
      background: url('../../assets/img/l-star@3x.png') no-repeat top left;
      background-size: cover;
    }
  }
}
</style>



