<template lang="html">
  <div class="tool-line">
    <img :src="collectImg" alt="" @click.stop="collection()">
    <!-- <img @click="toIgnore" src="../../assets/img/lahei-@3x.png" alt=""> -->
    <!-- <span>添加</span> -->
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import gray from '@/assets/img/Collection-2@2x.png'
import red from '@/assets/img/Collection@2x.png'

export default {
  props: {
    isCollect: '',
    collectId: ''
  },
  computed: {
    userId: function () {
      return utils.userId()
    },
    collectImg: function () {
      return this.isCollect ? red : gray
    }
  },
  methods: {
    toIgnore () {
      minutils.Toast('测试中。。')
      // minutils.MessageBoxConfirm({
      //   message: '拉入黑名单后, 该商户无法向你拨打电话, 并且名片资料无法显示',
      //   callback: function (action) {
      //     action && console.log(action)
      //   }
      // })
    },
    collection () {
      // 删除或保存收藏
      if (this.isCollect) {
        this.deleteCollection(this.userId, this.collectId)
        console.log('calcle')
      } else {
        this.saveCollection()
      }
    },
    saveCollection () { // 保存收藏
      let self = this
      minutils.MessageBoxConfirm({
        message: '是否确认收藏？',
        title: '提示',
        options: {
          confirmButtonText: '确认',
          cancelButtonText: '取消'
        },
        success (action) {
          self.$router.push('/movefriend')
        },
        fail (action) {
          console.log(action)
        }
      })
    },
    deleteCollection (userId, collectId) {
      let self = this
      minutils.MessageBoxConfirm({
        message: '是否取消收藏？',
        title: '提示',
        options: {
          confirmButtonText: '确认',
          cancelButtonText: '取消'
        },
        success (action) {
          // minutils.Toast('取消失败')
          api.delCollectionById({userId: userId, ids: collectId}).then((response) => {
            minutils.Toast('取消成功')
            console.log(self.isCollect)
            self.$emit('reload')
          })
        },
        fail (action) {
          console.log(action)
        }
      })
    }
  }
}
</script>

<style lang="less">
  .tool-line {
    img {
      width: .65rem;
      height: .85rem;
      padding: .2rem;
      padding-right: 0;
      // margin-right: .3rem;
    }
    span {
      position: relative;
      top: -0.14rem;
    }
  }
</style>



