<template lang="html">
  <div class="app">
    <default-head header="联系人详情" path="/search"></default-head>
    <div class="detail-page-show" v-if="userInfo.userDetails.userId">
      <div class="detail-page">
        <detail-head :headPic="this.headPic" :showMsg="!this.userInfo.userDetails.userType"></detail-head>
        <info-line :icon="vIcon">
          <div class="info-box">
            <div class="info-box__left">
              <span class="info-box__left__name" v-if="userInfo.userDetails.realName || userInfo.userDetails.nickName">{{userInfo.userDetails.realName || userInfo.userDetails.nickName || '暂无名称'}}</span>
              <span class="info-box__left__name" v-else>{{userInfo.userDetails.name}}</span>
              <!-- <span v-else>{{userInfo.userDetails.name}}</span> -->
              <b class="v--left v-gary" :class="{'v-yellow': userInfo.userDetails.vipLevel}">V</b><span class="v-gary" :class="{'v-yellow': userInfo.userDetails.vipLevel}">{{userInfo.userDetails.vipLevel}}</span>
            </div>
            <div class="info-box__right">{{this.userType}}</div>
          </div>
        </info-line>
        <info-line :icon="picList[2]" @click.native="call">
          <div class="info-box">
            <div class="info-box__left">
              <div>{{userInfo.userDetails.mobile}}</div>
              <div class="info-box__address">{{userInfo.userDetails.mobilePlace}}</div>
            </div>
            <div class="info-box__right">
              <tool-line :isCollect="userInfo.isCollect" :collectId="userInfo.collectId" @reload="reload"></tool-line>
            </div>
          </div>
        </info-line>
        <info-line :icon="picList[3]" >
          <div class="info-box" @click="storeMapLocation">
            <div class="info-box__left--address">
              <div class="">{{userInfo.address && userInfo.address.address}}</div>
            </div>
            <div class="info-box__right">
              <img class="icon" src="../assets/img/y01@3x.png" alt="">
            </div>
          </div>
        </info-line>
        <call-phone :times="userInfo.commentTotal.callCount" :praiseRate="userInfo.commentTotal.goodCommentRate"></call-phone>
        <appraise :servicePercent="this.preScore" :phonePercent="this.passRate" :totalScore="userInfo.commentTotal.totalRate"></appraise>
      </div>
      <type-list :data="this.filterTag"></type-list>
      <server :percent="this.userComment" v-if="userInfo.userComment"></server>
      <share @store-complain="storeComplain"></share>
      <upload-btn></upload-btn>
    </div>
    <div class="detail-page-show" v-else="userInfo.userDetails.userId">
      <no-data></no-data>
    </div>
  </div>
</template>

<script>
import api from '@/api'
import minutils from '@/utils/minUtils'
import utils from '@/utils/utils'
import noData from '@/components/common/noData'
import defaultHead from '@/components/head/defaultHead'
import detailHead from '@/components/detail/head'
import infoLine from '@/components/detail/infoLine'
import toolLine from '@/components/detail/toolLine'
import callPhone from '@/components/detail/callPhone'
import appraise from '@/components/detail/appraise'
import typeList from '@/components/detail/typeList'
import server from '@/components/detail/server'
import share from '@/components/detail/share'
import uploadBtn from '@/components/detail/uploadBtn'

import v0 from '@/assets/img/level/v-gray@2x.png'
import v1 from '@/assets/img/level/v-yellow@2x.png'
import phone from '@/assets/img/ip@3x.png'
import address from '@/assets/img/ad@3x.png'
import man from '@/assets/img/nan@2x.png'
import woman from '@/assets/img/nv@2x.png'
import hzl from '@/assets/img/hzl@2x.png'
export default {
  data () {
    return {
      picList: [v0, v1, phone, address],
      userInfo: {
        'userComment': 0,
        'isCollect': 0,
        'userTags': [
          {
            'id': null,
            'name': ''
          }
        ],
        'commentTotal': {
          'callCount': 0,
          'goodCommentRate': '',
          'passRate': '',
          'preScore': '',
          'totalRate': ''
        },
        'userDetails': {
          'address': '',
          'area': '',
          'city': '',
          'headerImg': '',
          'id': null,
          'level': 0,
          'mobile': '',
          'province': '',
          'sex': null,
          'type': 0,
          'userId': null
        }
      }
    }
  },
  components: {
    defaultHead,
    detailHead,
    infoLine,
    toolLine,
    callPhone,
    appraise,
    typeList,
    server,
    share,
    uploadBtn,
    noData
  },
  computed: {
    headPic: function () {
      if (parseInt(this.userInfo.userDetails.userType) === 1) return this.userInfo.userDetails.logo || hzl
      return this.userInfo.userComment ? this.userInfo.userDetails.facePic || hzl : this.userInfo.userDetails.sex === 1 ? man : woman
    },
    userType: function () {
      return this.userInfo.userDetails.userType ? this.userInfo.userDetails.enterpriseType === 1 ? '私营店主' : '企业' : '个人'
    },
    searchId: function () {
      return sessionStorage.getItem('searchId')
    },
    searchType: function () {
      return sessionStorage.getItem('searchType')
    },
    userId: function () {
      return utils.userId()
    },
    userComment: function () {
      if (this.userInfo.userComment > 5) return
      return (parseInt(this.userInfo.userComment) / 5) * 100 + '%'
    },
    filterTag: function () {
      return this.userInfo.userTags.slice(0, 10)
    },
    vIcon: function () {
      return this.userInfo.userDetails.level ? this.picList[1] : this.picList[0]
    }
  },
  mounted () {
    this.getUserDetail(this.userId, this.searchId)
  },
  methods: {
    reload () {
      this.getUserDetail(this.userId, this.searchId)
    },
    call () {
      let self = this
      minutils.MessageBoxConfirm({
        message: '确认拨打商户电话？',
        title: '提示',
        options: {
          confirmButtonText: '确认',
          cancelButtonText: '取消'
        },
        success (action) {
          let item = self.userInfo
          sessionStorage.setItem('searchId', item.userDetails.userId)
          self.saveCall(self.userId, item.userDetails.userId)
          // 打电话
          window.location.href = 'tel://' + item.userDetails.mobile
          self.$router.push('/appraiseorder')
        },
        fail () {
        }
      })
    },
    saveCall (userId, callId) {
      api.callNotesSave({
        'userId': userId,
        'byUserId': callId,
        'talkTime': 1,
        'fee': 0
      }).then((response) => {
      })
    },
    getUserDetail (loginId, userId) {
      api.getUserDetail({loginId: loginId, userId: userId}).then((response) => {
        let result = response.body
        if (result.isSuccess) {
          this.userInfo = result.data
        }
        this.preScore = parseFloat(this.userInfo.commentTotal.preScore) / 5 * 100 + '%'
        this.passRate = parseFloat(this.userInfo.commentTotal.passRate) + '%'
      })
    },
    getCompanyDetail (id, loginId) {
      api.getCompanyDetail({id: id, loginId: loginId}).then((response) => {
        let result = response.body
        if (result.isSuccess) {
          this.userInfo = result.data
          this.userInfo.userTags = result.data.enterpriseTags
          this.userInfo.userDetails = result.data.enterprise
        }
      })
    },
    storeComplain () {
      let complainData = {
        type: this.userInfo.userDetails.type,
        realName: this.userInfo.userDetails.realName,
        mobile: this.userInfo.userDetails.mobile
      }
      sessionStorage.setItem('complainData', JSON.stringify(complainData))
    },
    storeMapLocation () {
      let mapLocation = {
        lat: this.userInfo.address.lat,
        lng: this.userInfo.address.lon
      }
      sessionStorage.setItem('mapLocation', JSON.stringify(mapLocation))
      this.$router.push('/detailmap')
    }
  }
}
</script>

<style lang="less" scoped>
  @import '../assets/css/variable.less';

  body {
    background: @bgColor;
  }
  .detail-page-show {
    margin-top: .88rem;
  }
  .detail-page {
    background: @whiteColor;
  }
  .info-box {
    width: 100%;
    overflow: hidden;
    position: relative;
    font-size: .32rem;
    color: @defaultColor;
    .info-box__left {
      .info-box__left__name {
        display: inline-block;
        max-width: 4.6rem;
        height: .3rem;
        line-height: .36rem;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
      }
      .v-gary {
        color: @lightColor;
        font-size: .24rem;
      }
      .v-yellow {
        color: @orangeColor;
        font-size: .24rem;
      }
      .v--left {
        font-size: .28rem;
        margin-left: .1rem;
      }
      .info-box__address {
        margin-top: 0.1rem;
        font-size: .28rem;
        color: @lightColor;
      }
    }
    .info-box__left--address {
      margin-right: 1.8rem;
      line-height: 0.45rem;
    }
    .info-box__right {
      color: @midleColor;
      font-size: .3rem;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      .icon {
        width: 0.34rem;
        height: 0.50rem;
      }
    }
  }
</style>



