<template lang="html">
  <div class="info-line">
    <div class="info-line__left" @click="$emit('clickIcon')">
      <img class="info-line__img" :src="icon" alt="">
    </div>
    <div class="info-line__right">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    icon: {
      type: String,
      required: true
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';

.info-line {
  padding: 0.2rem 0.2rem;
  border-bottom: 1px solid @lineColor;
  background: @whiteColor;
  overflow: hidden;
  display: table;
  width: 100%;
  table-layout: fixed;
  .info-line__img {
    width: 0.44rem;
    height: 0.44rem;
  }
  .info-line__left {
    display: table-cell;
    width: 0.54rem;
    vertical-align: middle;
  }
  .info-line__right {
    overflow: hidden;
    display: table-cell;
    vertical-align: middle;
  }
}
</style>



