<template lang="html">
  <div class="upload-btn-box">
    <button class="upload-btn" type="button" name="button" @click="goTo('personcard')">
      想让别人找到你, 现在上传名片
    </button>
  </div>
</template>

<script>
export default {
  methods: {
    goTo (pathname) {
      this.$router.push(pathname)
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';

.upload-btn-box {
  margin-top: .3rem;
  padding: .24rem;
  .upload-btn {
    padding: .28rem;
    color: @whiteColor;
    background-color: @greenBtn;
    font-size: .34rem;
    width: 100%;
    border: 1px solid @greenBtn;
    border-radius: .08rem;
  }
}
</style>



