<template lang="html">
  <div class="share-block">
    <div class="itm" v-if="false">信息靠谱, 分享给朋友</div>
    <div class="itm" @click="goTo('/complaintform')">信息不靠谱, 投诉</div>
  </div>
</template>

<script>
export default {
  methods: {
    goTo (pathname) {
      this.$emit('store-complain')
      this.$router.push(pathname)
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';
  .share-block {
    background: @whiteColor;
    margin-top: .3rem;
    line-height: .88rem;
    font-size: .32rem;
    text-align: center;
    .itm:first-child {
      border-bottom: 1px solid @lineColor;
    }
  }
</style>



