<template lang="html">
  <div class="call-phone">
    <div class="l">
      <img src="../../assets/img/ip@3x.png" alt="">
      <div class="text">
        累计被拨打{{times}}次
      </div>
    </div>
    <div class="r">
      <img src="../../assets/img/Fabulous@3x.png" alt="">
      <div class="text">
        累计好评率{{praiseRate}}%
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    times: {
      type: Number | String,
      default: ''
    },
    praiseRate: {
      type: Number | String,
      default: ''
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';
 .call-phone {
   padding: 0.3rem 0.2rem;
   border-bottom: 1px solid @lineColor;
   color: @defaultColor;
   font-size: .3rem;
   overflow: hidden;
   .l {
     width: 50%;
     float: left;
     border-right: solid 1px @lineColor;
   }
   .r {
     float: left;
     padding-left: .1rem;
   }
   img {
     width: .44rem;
     height: .44rem;
     margin-right: .14rem;
     float: left;
     margin-top: -0.08rem;
   }
   .text {
     overflow: hidden;
   }
 }

</style>



