<template lang="html">
  <div class="type-list">
    <div class="list">
      <div class="item" v-for="(item, index) in data">
        <div class="block">
          {{index+1}}/{{item.name}}
        </div>
      </div>

    </div>
    <div class="more" v-if="false">
      <span>更多</span>
    </div>
  </div>
</template>

<script>

export default {
  props: {
    data: {
      type: Array,
      default: []
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';

.type-list {
  margin-top: .3rem;
  background: @whiteColor;
  color:@greenBorder;
  font-size: .32rem;
  .list {
    overflow: hidden;
    .item {
      width: 50%;
      padding: .15rem;
      float: left;
      .block {
        width: 100%;
        padding: 0 .2rem;
        border: solid 1px @greenBorder;
        height: 0.88rem;
        line-height: 0.88rem;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        border-radius: .06rem;
        text-align: center;
      }
    }
  }
  .more {
    padding: .2rem .15rem .3rem .15rem;
    text-align: right
  }
}
</style>



